<template>
  <footer class="footer">
    <audio :src="audioUrl" controls autoplay></audio>
  </footer>
</template>

<script>
export default {
  mounted() {
    this.$bus.$on('sendSangUrl', this.getSangUrl)
  },
  methods: {
    getSangUrl(url) {
      // console.log(url)
      this.audioUrl = url
    },
  },
  data() {
    return {
      // 播放器的url地址
      audioUrl: '',
    }
  },
}
</script>

<style lang="less" scoped>
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 10vh;
  background-color: white;
  border-top: 1px solid #ccc;

  audio {
    width: 100%;
    height: 100%;
  }
}
</style>
