<template>
  <div class="singer-info">
    <div class="singer-top">
      <img :src="artistList.img1v1Url" alt="" />
      <div class="singer-details">
        <h2>{{ artistList.name }}</h2>
        <p class="english-name">{{ alias }}</p>
        <div class="songs-info">
          <p>单曲数:{{ artistList.musicSize }}</p>

          <p>专辑:{{ artistList.albumSize }}</p>
          <p>MV:{{ artistList.mvSize }}</p>
        </div>
        <p class="singer-intro" @click="showSingerIntro">{{ artistList.briefDesc }}</p>
      </div>
    </div>

    <!-- 歌手热门歌曲 -->
    <!-- 歌曲列表 -->
    <el-tabs v-model="activeName">
      <el-tab-pane label="歌曲列表" name="singleSing">
        <div class="list-box">
          <img style="width: 150px; height: 150px" src="../assets/top50.png" alt="" />
          <!-- <el-table @row-dblclick="searchSingById" stripe ref="singleTable" :data="allSingList" highlight-current-row style="width: 100%"> -->
          <el-table @row-dblclick="searchSingById" :show-header="false" stripe ref="singleTable" :data="singerHotSongsList" highlight-current-row style="width: 100%">
            <el-table-column align="center" width="40" type="">
              <template slot-scope="scope">
                <i @click="searchSingById(scope.row)" class="el-icon-video-play"></i>
              </template>
            </el-table-column>
            <!-- <el-table-column align="center" width="50">
            <template>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-download"></i>
            </template>
          </el-table-column> -->

            <el-table-column label="音乐标题" width="">
              <template v-slot="scope">
                <div class="song-name">
                  {{ scope.row.name }}
                </div>
              </template>
            </el-table-column>
            <el-table-column label="时间" width="150px">
              <template v-slot="scope">
                <div class="song-time">
                  {{ scope.row.dt | timeFormat }}
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <!-- <el-pagination @current-change="handleCurrentChange" background :page-size="10" layout="prev, pager, next" :total="100"> </el-pagination> -->
      </el-tab-pane>
      <el-tab-pane label="MV" name="video">
        <div class="video">
          <div class="video-small" v-for="item in singerMvList" :key="item.id">
            <div class="video-img">
              <img @click="showMvDialog(item.id)" v-lazy="item.cover" alt="" />
              <div class="screen"><i class="el-icon-video-play"></i> {{ item.playCount | numberTextFormat }}</div>
              <div class="time-screen"><i class="el-icon-time-play"></i> {{ item.duration | timeFormat }}</div>
            </div>
            <div class="video-info">
              <p class="video-keywords"><span>MV</span>{{ item.name }}</p>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <!-- 歌手介绍详情框 -->
    <el-dialog :visible.sync="singerIntroDialogVisible" width="50%" :close="singerIntroClose">
      <span>{{ artistList.briefDesc }}</span>
      <!-- <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span> -->
    </el-dialog>
    <!-- 歌手mv -->
    <el-dialog class="mv-dialog" :visible.sync="mvDialogVisible" :close-on-click-modal="false" :close="closeEvent" width="50%">
      <video v-if="mvDialogVisible" autoplay :src="mvInfo.url" controls></video>
    </el-dialog>
  </div>
</template>

<script>
export default {
  watch: {
    activeName(val) {
      if (val == 'video') {
        this.getSingerMv()
      }
    },
  },
  data() {
    return {
      // 默认激活tab
      activeName: 'singleSing',
      // 歌手所有信息
      singerInfo: [],
      // 歌手介绍
      artistList: [],
      // 歌手详情展示框
      singerIntroDialogVisible: false,
      // 歌手mv
      mvDialogVisible: false,
      // 歌手热门歌曲
      singerHotSongsList: [],

      alias: '',
      // 歌手名字
      artistName: '',
      // 歌手相关mv数据
      singerMvList: [],
      // mv数据
      mvInfo: [],
    }
  },
  mounted() {
    // console.log(this.$route.query.id)
    // this.getSingerInfo()
    this.getUserSongs()
  },
  methods: {
    // 通过路由参数传来的歌手id获取歌手详情信息
    // async getSingerInfo() {
    //   const { data: res } = await this.$http.get(`/artist/detail?id=${this.$route.query.id}`)
    //   if (res.code !== 200) return this.$message.error('获取歌手信息失败，请刷新重试！')
    //   // console.log(res)
    //   this.singerInfo = res.data
    //   this.artistList = res.data.artist
    //   // console.log(this.singerInfo)
    // },
    async searchSingById(val) {
      // console.log(val)
      // 用post请求 在请求体中加上cookie值
      if (window.sessionStorage.getItem('cookie') == null) {
        this.$message.info('获取列表失败！请查看是否登录！')
        const { data: res } = await this.$http.post(`/song/url?id=${val.id}`)
        // console.log(res)
        // console.log('我没登陆听的歌手歌曲', res)
        if (res.code !== 200) return this.$message.info('获取列表失败！请查看是否登录！')
        this.sangInfo = res.data
        // console.log(this.sangInfo)
        // 获取到歌曲信息 判断是否为会员专享和没有歌曲信息
        if (this.sangInfo[0].code != 200) return this.$message.error('该歌曲必须登录或者是会员专享！')

        // 通过全局事件总线传递数据
        this.$bus.$emit('sendSangUrl', this.sangInfo[0].url)
        // this.audioUrl =
      } else {
        const { data: res } = await this.$http.post(`/song/url?id=${val.id}`, { cookie: window.sessionStorage.getItem('cookie') })
        // console.log(res)
        // console.log('我登陆了听的歌手歌曲', res)
        if (res.code !== 200) return this.$message.info('获取列表失败！请查看是否登录！')
        this.sangInfo = res.data
        // console.log(this.sangInfo)
        // 获取到歌曲信息 判断是否为会员专享和没有歌曲信息
        if (this.sangInfo[0].code != 200) return this.$message.error('该歌曲必须登录或者是会员专享！')

        // 通过全局事件总线传递数据
        this.$bus.$emit('sendSangUrl', this.sangInfo[0].url)
        // this.audioUrl =
      }
    },
    // 获取歌手热门歌曲
    async getUserSongs() {
      const { data: res } = await this.$http.get(`/artists?id=${this.$route.query.id}`)
      // console.log(res)
      if (res.code !== 200) return this.$message.error('获取歌手信息失败，请刷新重试！')
      this.artistList = res.artist
      if (res.artist.alias.length == 0) {
        // return
      } else {
        this.alias = res.artist.alias[0]
      }
      this.singerHotSongsList = res.hotSongs
      this.artistName = res.artist.name
      // console.log(this.artistName)
    },

    // 获取歌手相关MV
    async getSingerMv() {
      // 获取歌手的热门mv
      const { data: res } = await this.$http.get(`/search?keywords=${this.artistName}&type=1004&limit=100`)
      // console.log(res.result.mvs)
      this.singerMvList = res.result.mvs
    },

    // 点击展示歌手详情介绍
    showSingerIntro() {
      this.singerIntroDialogVisible = true
    },

    // 歌手详情介绍对话框关闭事件
    singerIntroClose() {},

    // 点击mv盒子播放
    async showMvDialog(id) {
      this.mvDialogVisible = true
      const { data: res } = await this.$http.get('/mv/url?id=' + id)
      // console.log(res)
      this.mvInfo = res.data
    },

    // 关闭盒子事件
    closeEvent() {
      this.mvDialogVisible = false
    },
  },
}
</script>

<style lang="less" scoped>
/deep/.el-dialog__header,
/deep/.el-dialog__body {
  background-color: rgb(0, 0, 0) !important;
}
i {
  font-size: 16px;
  color: rgb(158, 155, 155);
  // margin-top: 5px;
}
.el-table__row:hover i {
  color: black;
}
i:hover {
  cursor: pointer;
}
.list-box {
  display: flex;

  .el-table {
    flex-grow: 1;
    margin-left: 50px;
    .song-time {
      font-size: 13px;
      color: #b0adad;
    }
    .song-name {
      color: black;
      font-size: 15px;
      font-weight: 600;
    }
    .singer-name {
      a {
        color: rgb(59, 54, 119);
      }
      a:hover {
        color: black;
        border-bottom: 1px solid rgb(48, 63, 201);
      }
    }
  }
}
/deep/.el-tabs__item.is-active {
  font-size: 18px;
  font-weight: 500;
  color: #ff85a6 !important;
}
/deep/.el-tabs__item:hover {
  color: #ff85a6;
  cursor: pointer;
}
/deep/.el-tabs__active-bar {
  background-color: #ff85a6;
}
/deep/.el-tabs__nav-wrap::after {
  width: 0;
}
.singer-info {
  height: 220px;
  // width: 220px;

  .singer-top {
    margin-bottom: 10px;
    display: flex;
    .singer-details {
      margin-left: 20px;
      flex-grow: 1;
      .songs-info {
        margin: 15px 0 10px 0;
        font-size: 13px;
        display: flex;
        p {
          margin-right: 15px;
        }
      }
      .english-name {
        margin: 15px 0;
      }
      .singer-intro:hover {
        color: black;
        cursor: pointer;
      }
      .singer-intro {
        color: rgb(148, 141, 141);
        font-size: 14px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }
    }
  }
}
video {
  width: 100%;
  height: 100%;
}
.video {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .video-small {
    position: relative;
    display: flex;
    width: 275px;
    height: 250px;
    border-radius: 5px;
    // overflow: hidden;
    flex-direction: column;

    .video-info {
      // width: 0;
      // flex: 1;
      p {
        width: 100%;
        // flex: 1;
        font-weight: normal;
        // width: 270px;
      }
      .video-keywords {
        font-size: 14px;
        // display: flex;
        width: 100%;
        // max-width: 280px;
        align-items: center;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin: 2px 0px 3px 0;
        span {
          font-size: 12px;
          display: inline-block;
          border: 1px solid #ff85a6;
          color: #ff85a6;
          font-weight: 600;
          border-radius: 3px;
          margin-right: 3px;
        }
      }
    }
    .video-img {
      cursor: pointer;
      position: relative;
      // height: 180px;
      .screen {
        position: absolute;
        top: 0;
        right: 5px;
        color: white;
        font-size: 13px;
      }
      .time-screen {
        position: absolute;
        bottom: 6px;
        right: 5px;
        color: white;
        font-size: 13px;
      }
      img {
        width: 100%;
        height: 180px;
      }
    }
  }
}
</style>
