<template>
  <div class="search">
    <!-- <h2 class="sang-name">{{ this.$router.currentRoute.query.key }}</h2> -->
    <h3>
      找到 <span>{{ showTitle }}</span> {{ toggleWords }}
      <!-- <h2>{{ this.$router.currentRoute.query.key }}</h2> -->
    </h3>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="单曲" name="singleSing">
        <p v-if="isFlag">最佳匹配</p>
        <router-link class="singer-link" :to="{ name: 'singerinfo', query: { id: sangUserInfo.id } }"
          ><div v-if="isFlag" class="singer-box">
            <img :src="sangUserInfo.picUrl" alt="" />
            <p>
              歌手：{{ sangUserInfo.name }} <span v-for="(i, index) in sangUserInfo.alias" :key="index"> {{ i }}</span> <i class="el-icon-arrow-right"></i>
            </p></div
        ></router-link>

        <!-- 歌曲列表显示 -->
        <el-table @row-dblclick="searchSingById" stripe ref="singleTable" :data="sangList.songs" highlight-current-row style="width: 100%">
          <el-table-column align="center" property="" width="50" type="index">
            <template slot-scope="scope"> <i @click="searchSingById(scope.row)" class="el-icon-video-play"></i> </template
          ></el-table-column>
          <!-- <el-table-column align="center" property="" width="50"> -->
          <!-- <template>
              <i class="el-icon-star-off"></i>
              <i class="el-icon-download"></i>
            </template>
          </el-table-column> -->

          <el-table-column label="音乐标题" width="450">
            <template v-slot="scope">
              <div class="song-name">
                {{ scope.row.name }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="歌手" width="220">
            <template v-slot="scope">
              <div class="singer-name">
                <router-link :to="{ name: 'singerinfo', query: { id: scope.row.artists[0].id } }">{{ scope.row.artists[0].name }}</router-link>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="专辑">
            <template v-slot="scope">
              <div>
                {{ scope.row.album.name }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="时间" width="120">
            <template v-slot="scope">
              <div class="song-time">
                {{ scope.row.duration | timeFormat }}
              </div>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <!-- 热门歌曲第一首评论 -->
      <el-tab-pane label="评论" name="comment">
        <div class="comment-box" v-for="item in commentsList" :key="item.commentId">
          <img :src="item.user.avatarUrl" alt="" />
          <div class="content-box">
            <p>
              <a href="#">{{ item.user.nickname }}：</a><span>{{ item.content }}</span>
            </p>
            <p class="publishTime">{{ item.timeStr }}<i></i></p>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="视频(MV)" name="video">
        <div class="video">
          <div class="video-small" v-for="item in wordsMvList.mvs" :key="item.id">
            <div class="video-img">
              <img @click="showMvDialog(item.id)" v-lazy="item.cover" alt="" />
              <div class="screen"><i class="el-icon-video-play"></i> {{ item.playCount | numberTextFormat }}</div>
              <div class="time-screen"><i class="el-icon-time-play"></i> {{ item.duration | timeFormat }}</div>
            </div>
            <div class="video-info">
              <p class="video-keywords"><span>MV</span>{{ item.name }}</p>
              <p class="singer">
                <router-link :to="{ name: 'singerinfo', query: { id: item.artistId } }">{{ item.artistName }}</router-link>
              </p>
            </div>
          </div>
        </div>
        <el-dialog :visible.sync="mvDialogVisible" :close="closeEvent" width="50%">
          <video v-if="mvDialogVisible" autoplay :src="mvInfo.url" controls></video>
        </el-dialog>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  mounted() {
    // this.$bus.$on('sendUserList', this.getUserList)
    // this.$bus.$on('getSangList', this.getUserList)
    // console.log(this.$router.currentRoute.query.key)
    this.keyId = this.$router.currentRoute.query.key
    this.getUserList(this.$router.currentRoute.query.key)
    // console.log()
    // console.log(this.$router.currentRoute.query.key)
  },
  updated() {
    // console.log(this.$router.currentRoute.query.key)
  },
  data() {
    return {
      // 关键词搜索到的歌手信息
      sangUserInfo: [],
      activeName: 'singleSing',
      sangList: {},
      // 歌手列表框是否展示布尔值
      isFlag: false,
      // 双击歌曲行获取歌曲所有数据
      sangInfo: [],
      // 获取第一首歌曲id
      firstSangId: '',
      // 热门歌曲评论列表信息
      commentsList: [],
      keyId: '',
      // 默认展示单曲字眼
      toggleWords: '首单曲',

      // 关键词对应的MV列表
      wordsMvList: [],

      // 控制mv展示的布尔值
      mvDialogVisible: false,

      // mv信息
      mvInfo: [],
    }
  },
  watch: {
    $route(to, from) {
      //将mounted中的数据在这里重新加载一下即可
      // console.log(to, from)
      // console.log(this.$route)
      // console.log(to.query.key)
      this.keyId = to.query.key
      if (!from.query.key) return
      this.activeName = 'singleSing'
      this.getUserList(to.query.key)
    },
    keyId(val) {
      // this.getUserList(val)
    },
    // 监听tabs变化
    activeName(val) {
      // console.log(this.sangList.songs[0].id)
      if (val == 'comment') {
        // if (this.commentsList) return
        this.getSangComment()
      } else if (val == 'singleSing') {
        if (this.sangList) return
        this.getUserList(this.$router.currentRoute.query.key)
      } else if (val == 'video') {
        this.getSangMv()
      }
    },
    mvDialogVisible(val) {
      // console.log(val)
      if (val == 'false') {
        // let video = document.getElementById('video')
        // video.ended()
        this.$refs.vueMiniPlayer.pause()
      }
    },
  },

  methods: {
    // 获取歌手信息
    async getUserList(val) {
      if (window.sessionStorage.getItem('cookie') == null) {
        this.$message.info('获取列表失败！请查看是否登录！')
        const { data: res } = await this.$http.post(`search/multimatch?keywords=${val}&timestamp=${new Date()}`)
        // console.log('我没登陆搜索到的', res)
        const { data: res1 } = await this.$http.get(`search?keywords=${val}&timestamp=${new Date()}`)
        // console.log(encodeURIComponent(window.sessionStorage.getItem('cookie')))
        // console.log(res, res1)
        // 判断orders的长度如果为0 就没有歌手信息 不展示歌手列表框
        if (res.code !== 200) return this.$message.info('请先登录！')
        if (res1.code !== 200) return this.$message.info('获取列表失败！请查看是否登录！')
        if (res.result.orders.length == 0 || res.result.orders[0] !== 'artist') {
          // this.sangList = val1
          this.isFlag = false
          this.sangList = res1.result
        } else {
          this.isFlag = true
          this.sangUserInfo = res.result.artist[0]
          this.sangList = res1.result
          // console.log(this.sangList)
          this.firstSangId = this.sangList.songs[0].id
          // console.log(this.sangUserInfo)
        }
      } else {
        const { data: res } = await this.$http.post(`search/multimatch?keywords=${val}&timestamp=${new Date()}`, { cookie: window.sessionStorage.getItem('cookie') })
        // console.log('我登录了搜索到的', res)
        const { data: res1 } = await this.$http.get(`search?keywords=${val}&timestamp=${new Date()}`)
        // console.log(encodeURIComponent(window.sessionStorage.getItem('cookie')))
        // console.log(res, res1)
        // 判断orders的长度如果为0 就没有歌手信息 不展示歌手列表框
        if (res.code !== 200) return this.$message.info('请先登录！')
        if (res1.code !== 200) return this.$message.info('获取列表失败！请查看是否登录！')
        if (res.result.orders.length == 0 || res.result.orders[0] !== 'artist') {
          // this.sangList = val1
          this.isFlag = false
          this.sangList = res1.result
        } else {
          this.isFlag = true
          this.sangUserInfo = res.result.artist[0]
          this.sangList = res1.result
          // console.log(this.sangList)
          this.firstSangId = this.sangList.songs[0].id
          // console.log(this.sangUserInfo)
        }
      }
    },
    // 获取传进来的id歌曲信息
    async searchSingById(val) {
      // console.log(val.id)
      // 用post请求 在请求体中加上cookie值
      if (window.sessionStorage.getItem('cookie') == null) {
        this.$message.info('获取列表失败！请查看是否登录！')
        const { data: res } = await this.$http.post(`/song/url?id=${val.id}`)
        // console.log(res)
        // console.log('我没登陆听搜索到的', res)
        if (res.code !== 200) return this.$message.info('获取列表失败！请查看是否登录！')
        this.sangInfo = res.data
        // console.log(this.sangInfo)
        // 获取到歌曲信息 判断是否为会员专享和没有歌曲信息
        if (this.sangInfo[0].code != 200) return this.$message.error('该歌曲必须登录或者是会员专享！')

        // 通过全局事件总线传递数据
        this.$bus.$emit('sendSangUrl', this.sangInfo[0].url)
        // this.audioUrl =
      } else {
        const { data: res } = await this.$http.post(`/song/url?id=${val.id}`, { cookie: encodeURIComponent(window.sessionStorage.getItem('cookie')) })
        // console.log('我登陆了听搜索到的', res)
        if (res.code !== 200) return this.$message.info('获取列表失败！请查看是否登录！')
        this.sangInfo = res.data
        // console.log(this.sangInfo)
        // 获取到歌曲信息 判断是否为会员专享和没有歌曲信息
        if (this.sangInfo[0].code != 200) return this.$message.error('该歌曲必须登录或者是会员专享！')

        // 通过全局事件总线传递数据
        this.$bus.$emit('sendSangUrl', this.sangInfo[0].url)
        // this.audioUrl =
      }
    },

    // 点击tabs切换事件
    handleClick() {
      // console.log(this.activeName)
      // if (this.commentsList) return
      // this.getSangComment()
    },

    // 获取第一首歌曲评论信息
    async getSangComment() {
      // console.log(this.firstSangId)
      const { data: res } = await this.$http.get(`comment/music?id=${this.firstSangId}&limit=20`)
      this.commentsList = res.comments
      // console.log(this.commentsList)
    },

    // 获取歌手的热门mv
    async getSangMv() {
      const { data: res } = await this.$http.get(`/search?keywords=${this.keyId}&type=1004&limit=100`)
      // console.log(res)
      this.wordsMvList = res.result
    },

    // 点击mv盒子打开对话框
    async showMvDialog(id) {
      // console.log(id)
      this.mvDialogVisible = true
      const { data: res } = await this.$http.get('/mv/url?id=' + id)
      // console.log(res)
      this.mvInfo = res.data
    },

    // 关闭mv盒子事件
    closeEvent() {
      this.showMvDialog = false
      // this.mvInfo = []
      this.$refs.vueMiniPlayer.pause()
    },
  },
  computed: {
    showTitle() {
      if (this.activeName == 'singleSing') {
        this.toggleWords = '首单曲'
        return this.sangList.songCount
      } else if (this.activeName == 'comment') {
        this.toggleWords = '条评论'
        return this.commentsList.length
      } else {
        this.toggleWords = '个MV'
        return this.wordsMvList.mvCount
      }
    },
  },
  // watch: {},
  beforeDestroy() {
    // this.$bus.$off('sendSangUrl')
  },
}
</script>

<style lang="less" scoped>
.singer-link {
  display: block;
}
/deep/.el-dialog__header,
/deep/.el-dialog__body {
  background-color: rgb(0, 0, 0) !important;
}
.song-time {
  font-size: 13px;
  color: #b0adad;
}
.song-name {
  color: black;
  font-size: 14px;
  font-weight: 600;
}
.singer-name {
  a {
    color: rgb(59, 54, 119);
  }
  a:hover {
    color: black;
    border-bottom: 1px solid rgb(48, 63, 201);
  }
}
.el-dialog__header,
.el-dialog__body {
  background-color: black;
}
/deep/.el-button--primary {
  color: #fff;
  background-color: #ff85a6;
  border-color: #ff85a6;
}
/deep/.el-button:focus,
.el-button:hover {
  color: #ff85a6;
  border-color: #ffbcce;
  background-color: #ffd9e29d;
}
/deep/.el-tabs__item.is-active {
  font-size: 18px;
  font-weight: 500;
  color: #ff85a6 !important;
}
/deep/.el-tabs__item:hover {
  color: #ff85a6;
  cursor: pointer;
}
/deep/.el-tabs__active-bar {
  background-color: #ff85a6;
}
/deep/.el-tabs__nav-wrap::after {
  width: 0;
}
/deep/.el-table .el-table__cell {
  padding: 5px 0;
}
/deep/.cell {
  font-size: 13px;
  color: rgb(116, 116, 116);
  font-weight: 500;
}
.search {
  video {
    width: 100%;
    height: 100%;
  }
  .video {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .video-small {
      position: relative;
      display: flex;
      width: 275px;
      height: 250px;
      border-radius: 5px;
      // overflow: hidden;
      flex-direction: column;

      .video-info {
        // width: 0;
        // flex: 1;
        p {
          width: 100%;
          // flex: 1;
          font-weight: normal;
          // width: 270px;
        }
        .singer {
          color: rgba(80, 125, 175);
          font-size: 12px;
          a {
            color: rgb(59, 54, 119);
          }
          a:hover {
            color: black;
            border-bottom: 1px solid rgb(48, 63, 201);
          }
        }
        .video-keywords {
          // display: flex;
          width: 100%;
          // max-width: 280px;
          align-items: center;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin: 2px 0px;
          span {
            display: inline-block;
            border: 1px solid #ff85a6;
            color: #ff85a6;
            font-weight: 600;
            border-radius: 5px;
            margin-right: 3px;
          }
        }
      }
      .video-img {
        cursor: pointer;
        position: relative;
        // height: 180px;
        .screen {
          position: absolute;
          top: 0;
          right: 5px;
          color: white;
          font-size: 13px;
        }
        .time-screen {
          position: absolute;
          bottom: 6px;
          right: 5px;
          color: white;
          font-size: 13px;
        }
        img {
          width: 100%;
          height: 180px;
        }
      }
    }
  }
  .comment-box {
    height: 75px;
    padding: 0 0 10px 0;
    border-bottom: 1px solid #ccc;
    display: flex;
    align-items: center;
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
    .content-box {
      flex-flow: 1;
      height: 40px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-left: 3px;
      p {
        font-size: 13px;
        span {
          font-size: 12px;
          font-weight: 400;
        }
      }
      .publishTime {
        font-size: 12px;
        color: #ccc;
        font-weight: 400;
      }
      a {
        color: rgb(255, 133, 166);
      }
    }
  }
  .sang-name {
    color: rgb(255, 133, 166);
  }
  h3 {
    margin-bottom: 10px;
    span {
      margin: 0 2px;
      color: rgb(255, 133, 166);
    }
  }
  p {
    font-size: 12px;
    color: rgb(75, 75, 75);
    font-weight: 600;
  }
  ul {
    margin: 20px 0;
    display: flex;
    li {
      margin-right: 30px;
      font-size: 16px;
      a {
        color: black;
      }
    }
  }
  .singer-box {
    cursor: pointer;
    width: 350px;
    height: 100px;
    display: flex;
    // border: 1px solid #ccc;
    align-items: center;
    border-radius: 5px;
    margin: 15px 0;
    background-color: rgba(238, 238, 238, 0.61);
    img {
      width: 100px;
      height: 100%;
      border-radius: 5px;
    }
    p {
      display: flex;
      position: relative;
      // justify-content: space-between;
      // flex-direction: row;
      align-items: center;
      flex-grow: 1;
      font-size: 14px;
      margin-left: 15px;
      i {
        // flex-direction: ;
        // flex-direction: row;
        // align-self: flex-end;
        position: absolute;
        right: 15px;
      }
      span {
        // flex-grow: ;
        color: rgb(94, 94, 94);
        font-weight: 400;
        margin-left: 10px;
      }
    }
  }
}
i {
  font-size: 15px;
  color: rgb(158, 155, 155);
  // margin-top: 5px;
}
.el-table__row:hover i {
  color: black;
}
i:hover {
  cursor: pointer;
}
</style>
