<template>
  <div class="top-nav">
    <!-- 头部logo -->
    <div class="logo">
      <h1>
        <router-link to="/">a</router-link>
      </h1>
      <!-- 前进后退按钮 -->
      <ul>
        <li>
          <a @click="$router.back(-1)"><i class="el-icon-arrow-left"></i></a>
        </li>
        <li>
          <a @click="$router.forward(1)"><i class="el-icon-arrow-right"></i></a>
        </li>
      </ul>
      <!-- 搜索歌曲输入框 -->
      <el-input placeholder="搜索歌曲" size="small" prefix-icon="el-icon-search" @keyup.enter.native="searchSingByName" v-model="keyWords"> </el-input>
    </div>
    <!-- 右边头部信息区域 -->
    <div class="intro">
      <!-- 用户头像和昵称 -->
      <div class="user" v-if="userInfo">
        <!-- 鼠标放到头像上展示信息 -->
        <el-popover placement="top-start" trigger="hover">
          <div class="show-box">
            <div class="gender">
              <h3>{{ userInfo.gender }}</h3>
              <p>动态</p>
            </div>
            <div class="new-follows">
              <h3>{{ userInfo.newFollows }}</h3>
              <p>关注</p>
            </div>
            <div class="follow-eds">
              <h3>{{ userInfo.followeds }}</h3>
              <p>粉丝</p>
            </div>
          </div>
          <div class="level">
            <ul>
              <li>
                <i class="el-icon-s-flag">介绍：</i> <span>{{ userInfo.signature }}</span>
              </li>
            </ul>
          </div>
          <template slot="reference">
            <div>
              <img v-lazy="userInfo.avatarUrl" alt="" />
              <span>{{ userInfo.nickname }}</span>
            </div>
          </template>
          <!-- <el-button slot="reference">hover 激活</el-button> -->
        </el-popover>
      </div>

      <!-- 登录和链接 -->
      <ul>
        <!-- <li><router-link to="">介绍</router-link></li>
        <li><router-link to="">github</router-link></li> -->
        <li v-if="loginFlag">
          <router-link to="/login"><el-button type="primary" size="small">登录</el-button></router-link>
        </li>
        <li><el-button @click="quit" type="info" size="small">退出</el-button></li>
      </ul>
    </div>
  </div>
</template>

<script>
// import axios from 'axios'
export default {
  computed: {
    loginFlag() {
      if (localStorage.getItem('UserId')) {
        return false
      } else {
        return true
      }
    },
  },
  mounted() {
    // 用户登录后再localStorage里根据id查询用户的信息
    this.getUserList()
  },
  data() {
    return {
      username: '春夏秋冬里的你',
      keyWords: '',
      // 登录成功后 用户的信息
      userInfo: {},
    }
  },
  methods: {
    // 根据关键词搜索歌曲或者歌手
    searchSingByName() {
      // const { data: res } = await this.$http.get(`search/multimatch?keywords=${this.keyWords}&timestamp=${new Date()}`)
      // const { data: res1 } = await this.$http.get(`search?keywords=${this.keyWords}&timestamp=${new Date()}`)
      // console.log(res1)
      // if (!res.result.artist) {
      //   this.$message.info('搜不到该歌手信息,已为您展示相关歌曲！')
      // }
      // this.$message.success('已成功展示对应歌手和歌曲！')
      // this.$bus.$emit('sendUserList', this.keyWords)
      // console.log(1)
      // this.$router.push('/doc/search')
      this.$router.push({ name: 'search', query: { key: this.keyWords } })
      // this.$bus.$emit('sendUserList', this.keyWords)
    },
    // 根据id查询用户信息
    async getUserList() {
      const { data: res } = await this.$http.post(`/user/detail?uid=${localStorage.getItem('UserId')}`, { cookie: encodeURIComponent(window.sessionStorage.getItem('cookie')) })
      // const { data: res } = await this.$http.post(`/user/detail?uid=${localStorage.getItem('UserId')}`, { withCredentials: true })

      // console.log(res.profile)
      this.userInfo = res.profile
    },
    // 退出事件
    quit() {
      window.sessionStorage.clear()
      window.localStorage.removeItem('UserId')
      this.$router.push('/login')
    },
  },
}
</script>

<style lang="less" scoped>
.top-nav {
  padding: 5px 5px;
  height: 8vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  // align-items: center;
  background-color: rgb(255, 133, 166);
  border-bottom: 1px solid rgb(255, 0, 0);
  z-index: 0;
  // box-shadow: -1px 0px 6px #b3a8b2bb;
  .logo {
    min-width: 500px;
    overflow: hidden;
    display: flex;
    align-items: center;
    ul {
      display: flex;
      margin-left: 5px;
      // min-width: 200px;
      li {
        a {
          cursor: pointer;
        }
        i {
          color: rgba(248, 205, 216);
        }

        min-height: 20px;
        min-width: 20px;
        border-radius: 50%;
        text-align: center;
        margin: 0 5px;
        // line-height: 15px;
        background-color: rgb(235, 112, 145);
      }
    }
    /deep/.el-input__inner {
      border-radius: 20px;
    }
    .el-input {
      margin-left: 10px;
      width: 250px;
    }
    h1 {
      width: 165px;
      margin-right: 80px;
      // height: 40px;
      // background-color: red;
      background: url(./assets/topbar.png) no-repeat 0 9999px;
      background-position: 0 -15px;
      a {
        display: block;
        height: 100%;
        width: 100%;
        text-indent: -9999px;
      }
    }
    // a {
    //   color: #5960e2;
    //   display: flex;
    //   align-items: center;
    //   justify-content: space-between;
    //   img {
    //     width: 40px;
    //     border-radius: 50%;
    //     // vertical-align: middle;
    //     margin-right: 10px;
    //   }
    // }
  }
  .intro {
    display: flex;
    align-items: center;
    margin-right: 70px;
    .user:hover {
      cursor: pointer;
    }
    .user:hover span {
      color: white;
      // cursor: pointer;
    }
    .user {
      display: flex;
      align-items: center;
      margin-right: 40px;

      img {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        vertical-align: middle;
      }

      span {
        color: rgb(255, 233, 255);
        font-size: 13px;
        margin-left: 5px;
        // width: 100px;

        /*超出部分就隐藏*/

        overflow: hidden;

        /*不换行设定*/

        white-space: nowrap;

        /*超出部分的文字显示省略号*/

        text-overflow: ellipsis;
      }
    }
    ul {
      display: flex;
      min-width: 300px;
      align-items: center;
      li {
        margin: 0 10px;
        color: #87e4d0;
        font-size: 14px;
        padding: 0 5px;
      }
    }
  }
}
</style>
