<template>
  <div class="home">
    <Top></Top>
    <div class="main">
      <Aside></Aside>
      <Main></Main>
    </div>
    <Footer></Footer>
    <!-- 右上角弹窗信息 -->
    <div class="info"><el-button class="info-btn" plain @click="open1"></el-button></div>
    <div class="info"><el-button class="info-btn" plain @click="open2"></el-button></div>
  </div>
</template>

<script>
import Top from '../components/Top.vue'
import Aside from '../components/Aside.vue'
import Main from '../components/Main.vue'
import Footer from '../components/Footer.vue'
export default {
  components: {
    Top,
    Aside,
    Main,
    Footer,
  },
  data() {
    return {
      isFlag: localStorage.getItem('UserId') ? true : false,
    }
  },
  methods: {
    open1() {
      const h = this.$createElement
      this.$notify.info({
        title: '提示',
        dangerouslyUseHTMLString: true,
        message: '<strong>本项目大部分功能需要登陆之后才可以正常使用！<br/>先进行<a style="color:rgb(252, 94, 136)" href="#/login"><i>登录/注册</i></a> 再重新体验吧！</strong>',
        offset: 100,
      })
    },
    open2() {
      this.$notify({
        title: '成功',
        dangerouslyUseHTMLString: true,
        message: '<strong>恭喜您！登陆成功，请体验！</strong>',
        type: 'success',
        offset: 100,
      })
    },
  },
  mounted() {
    if (this.isFlag) {
      this.open2()
    } else {
      this.open1()
    }
  },
  watch: {},
}
</script>

<style lang="less" scoped>
/deep/.el-notification.right {
  top: 50px !important;
}
.info-btn {
  z-index: -1;
}
.home {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  .main {
    display: flex;
    flex-grow: 1;

    // justify-content: space-between;
  }
}
</style>
